.movie_card {
  width: 800px;
  height: 350px;
  border-radius: 10px;
  margin: 100px auto;
  transition: all .4s;
  display: block;
  position: relative;
  overflow: hidden;
}

.movie_card:hover {
  transition: all .4s;
  transform: scale(1.02);
}

.movie_card .info_section {
  width: 100%;
  height: 100%;
  background-blend-mode: multiply;
  z-index: 2;
  border-radius: 10px;
  position: relative;
}

.movie_card .info_section .movie_header {
  height: 40%;
  padding: 25px;
  position: relative;
}

.movie_card .info_section .movie_header h1 {
  color: var(--accent-color);
  font-weight: 400;
}

.movie_card .info_section .movie_header h4 {
  color: #9ac7fa;
  font-weight: 400;
}

.movie_card .info_section .movie_header .minutes {
  color: #fff;
  border: 1px solid rgba(235, 89, 10, .106);
  border-radius: 5px;
  margin-top: 10px;
  padding: 5px;
  display: inline-block;
}

.movie_card .info_section .movie_header .type {
  color: #cee4fd;
  margin-left: 10px;
  display: inline-block;
}

.movie_card .info_section .movie_header .locandina {
  float: left;
  height: 120px;
  margin-right: 20px;
  position: relative;
