  .movie_card {
    width: 95%;
    min-height: 350px;
    height: auto;
    margin: 70px auto;
  }

  .blur_back {
    width: 100%;
    background-position: 50% !important;
  }

  .movie_header {
    width: 100%;
    margin-top: 85px;
  }

  .movie_desc {
    width: 100%;
  }

